:host .icon-button {
  align-items: center;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  max-width: 100%;
  text-align: center;
  text-decoration: none;
  justify-content: center;
  transition: color linear 0.2s;
  border-style: solid;
  padding: 0;

  &:focus-visible {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 2px;
  }

  &:hover {
    transition: var(--mdx-comp-icon-button-transition-hover-fade-in-out);
  }
}

