import { Component, Prop, h } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { ICON_BUTTON_AS, ICON_BUTTON_SIZES, ICON_BUTTON_TYPE, ICON_BUTTON_VARIANTS } from './exports';

/**
 * @slot - Icon
 */

@Component({
  tag: 'mdx-icon-button',
  styleUrl: 'icon-button.scss',
  shadow: true,
})
export class IconButton {
  @Prop({ reflect: true })
  rounded: boolean;

  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  variant: (typeof ICON_BUTTON_VARIANTS)[keyof typeof ICON_BUTTON_VARIANTS] = ICON_BUTTON_VARIANTS.PRIMARY;

  @Prop({ reflect: true })
  size: (typeof ICON_BUTTON_SIZES)[keyof typeof ICON_BUTTON_SIZES] = ICON_BUTTON_SIZES.MD;

  @Prop()
  type: (typeof ICON_BUTTON_TYPE)[keyof typeof ICON_BUTTON_TYPE] = ICON_BUTTON_TYPE.BUTTON;

  @Prop({ reflect: true })
  as: (typeof ICON_BUTTON_AS)[keyof typeof ICON_BUTTON_AS] = ICON_BUTTON_AS.BUTTON;

  @Prop()
  href: string;

  @Prop()
  // eslint-disable-next-line @stencil-community/reserved-member-names -- to enforece aria-label
  ariaLabel!: string;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    if (this.as === ICON_BUTTON_AS.BUTTON) {
      return (
        <button class="icon-button" type={this.type} disabled={this.disabled} aria-label={this.ariaLabel}>
          <slot></slot>
        </button>
      );
    }

    return (
      <a
        class="icon-button"
        href={this.href}
        aria-label={this.ariaLabel}
        target={this.target}
        rel={this.isExternalURL() ? 'noreferrer' : undefined}
      >
        <slot></slot>
      </a>
    );
  }
}
